/* Animation! */
:root {
  --circle-1-x: -33vw;
  --circle-1-y: -33vw;
  --circle-1-w: 75vw;

  --circle6-1-x: 0vw;
  --circle6-1-y: 35vw;
  --circle6-1-w: 80vw;

  --circle4-1-x: -25vw;
  --circle4-1-y: 50vw;
  --circle4-1-w: 70vw;

  --circle2-1-x: 50vw;
  --circle2-1-y: -15vw;
  --circle2-1-w: 50vw;

  --circle8-1-x: 50vw;
  --circle8-1-y: 70vw;
  --circle8-1-w: 10vw;
}

@-webkit-keyframes slide {
  from {
    left: var(--circle-1-x);
  }
  to {
    left: calc(150vw + var(--circle-1-x));

  }
}
@keyframes slide {
  from {
    left: var(--circle-1-x);
  }
  to {
    left: calc(150vw + var(--circle-1-x));
  }
}
@-webkit-keyframes slide2 {
  from {
    left: calc(-150vw + var(--circle-1-x));
  }
  to {
    left: var(--circle-1-x);
  }
}
@keyframes slide2 {
  from {
    left: calc(-150vw + var(--circle-1-x));
  }
  to {
    left: var(--circle-1-x);
  }
}

.circle {
  margin-top: var(--circle-1-y);
  width: var(--circle-1-w);
  height: var(--circle-1-w);
  border-radius: 50%;
  position: absolute;
  background-color: #ECE6DB;
  z-index: -1;

  -webkit-animation: slide 250s linear 0s infinite;
  animation: slide 250s linear 0s infinite;
}
.circle:nth-child(2) {
  -webkit-animation: slide2 250s linear 0s infinite;
  animation: slide2 250s linear 0s infinite;
}
