/* Animation! */
:root {



}

@-webkit-keyframes slideb {
  from {
    left: var(--circle4-1-x);
  }
  to {
    left: calc(150vw + var(--circle4-1-x));

  }
}
@keyframes slideb {
  from {
    left: var(--circle4-1-x);
  }
  to {
    left: calc(150vw + var(--circle4-1-x));
  }
}
@-webkit-keyframes slideb2 {
  from {
    left: calc(-150vw + var(--circle4-1-x));
  }
  to {
    left: var(--circle4-1-x);
  }
}
@keyframes slideb2 {
  from {
    left: calc(-150vw + var(--circle4-1-x));
  }
  to {
    left: var(--circle4-1-x);
  }
}

.circle4 {
  margin-top: var(--circle4-1-y);
  width: var(--circle4-1-w);
  height: var(--circle4-1-w);
  border-radius: 50%;
  position: absolute;
  background-color: #ECE6DB;
  z-index: -1;

  -webkit-animation: slideb 250s linear 0s infinite;
  animation: slideb 250s linear 0s infinite;
}
.circle5{
  margin-top: var(--circle4-1-y);
  width: var(--circle4-1-w);
  height: var(--circle4-1-w);
  border-radius: 50%;
  position: absolute;
  background-color: #ECE6DB;
  z-index: -1;



  -webkit-animation: slideb2 250s linear 0s infinite;
  animation: slideb2 250s linear 0s infinite;
}
