/* Animation! */
:root {

}

@-webkit-keyframes slidec {
  from {
    left: var(--circle2-1-x);
  }
  to {
    left: calc(150vw + var(--circle2-1-x));

  }
}
@keyframes slidec {
  from {
    left: var(--circle2-1-x);
  }
  to {
    left: calc(150vw + var(--circle2-1-x));
  }
}
@-webkit-keyframes slidec2 {
  from {
    left: calc(-150vw + var(--circle2-1-x));
  }
  to {
    left: var(--circle2-1-x);
  }
}
@keyframes slidec2 {
  from {
    left: calc(-150vw + var(--circle2-1-x));
  }
  to {
    left: var(--circle2-1-x);
  }
}

.circle2 {
  margin-top: var(--circle2-1-y);
  width: var(--circle2-1-w);
  height: var(--circle2-1-w);
  border-radius: 50%;
  position: absolute;
  background-color: #ECE6DB;
  z-index: -1;

  -webkit-animation: slidec 250s linear 0s infinite;
  animation: slidec 250s linear 0s infinite;
}
.circle3{
  margin-top: var(--circle2-1-y);
  width: var(--circle2-1-w);
  height: var(--circle2-1-w);
  border-radius: 50%;
  position: absolute;
  background-color: #ECE6DB;
  z-index: -1;



  -webkit-animation: slidec2 250s linear 0s infinite;
  animation: slidec2 250s linear 0s infinite;
}
