/* Animation! */
:root {



}

@-webkit-keyframes slidea {
  from {
    left: var(--circle8-1-x);
  }
  to {
    left: calc(150vw + var(--circle8-1-x));

  }
}
@keyframes slidea {
  from {
    left: var(--circle8-1-x);
  }
  to {
    left: calc(150vw + var(--circle8-1-x));
  }
}
@-webkit-keyframes slidea2 {
  from {
    left: calc(-150vw + var(--circle8-1-x));
  }
  to {
    left: var(--circle8-1-x);
  }
}
@keyframes slidea2 {
  from {
    left: calc(-150vw + var(--circle8-1-x));
  }
  to {
    left: var(--circle8-1-x);
  }
}

.circle8 {
  margin-top: var(--circle8-1-y);
  width: var(--circle8-1-w);
  height: var(--circle8-1-w);
  border-radius: 50%;
  position: absolute;
  background-color: #ECE6DB;
  z-index: -1;

  -webkit-animation: slidea 250s linear 0s infinite;
  animation: slidea 250s linear 0s infinite;
}
.circle9{
  margin-top: var(--circle8-1-y);
  width: var(--circle8-1-w);
  height: var(--circle8-1-w);
  border-radius: 50%;
  position: absolute;
  background-color: #ECE6DB;
  z-index: -1;



  -webkit-animation: slidea2 250s linear 0s infinite;
  animation: slidea2 250s linear 0s infinite;
}
